'use '

import Link from "next/link"







export default function Header() {
    return (
        <section className="  px-6 py-24 text-center max-w-7xl mx-auto z-10">
            <h1 className="text-4xl font-extrabold tracking-tight sm:text-5xl md:text-6xl lg:text-7xl
             max-w-4xl mx-auto text-white">
                Primeira usina siderúrgica carbono neutro do mundo</h1>

            <p className="mt-6 text-lg  max-w-2xl mx-auto text-white font-extrabold">Produzir aço de forma sustentável é possível — e estamos construindo esse futuro agora. Venha fazer parte disso conosco</p>

            <div className=" mt-10 flex flex-wrap justify-center gap-4" >
                <div className="flex-inline flex items-center gap-2 bg-black/95 rounded-xl p-1 shadow-lg">
                    <input
                        type="email"
                        placeholder="Entre com seu Email"
                        className="
                            w-64
                            px-4 py-3 
                            rounded-xl 
                            text-white
                            placeholder:text-white
                            focus:outline-none
                            focus:ring-2 focus:ring-green-300
            "
                    />
                   <Link 
  href="/login"
  className="
    px-6 py-3 
    rounded-xl
    bg-green-600 
    text-white 
    font-semibold
    inline-block
  "
>
  Entrar em sua conta
</Link>



                </div>
                <Link href ="/cadastro" className="
      px-6 py-3 
      rounded-xl 
      bg-black
      text-white
      font-medium
      backdrop-blur-sm
    ">Cadastre-se</Link>


            </div>
        </section>
    )
}
